"use client";
import React from "react";

type Props = {
  title: string;
  status: "idle" | "loading" | "success" | "error";
  content?: string;
  error?: string;
};

export default function ModelResponseCard({ title, status, content, error }: Props) {
  const badge =
    status === "success"
      ? "bg-green-500/15 text-green-300"
      : status === "error"
      ? "bg-red-500/15 text-red-300"
      : status === "loading"
      ? "bg-blue-500/15 text-blue-300"
      : "bg-white/10 text-neutral-300";

  return (
    <div className="response-card min-w-[320px] max-w-[420px] rounded-xl border border-white/10 bg-white/5 p-4 text-neutral-100 shadow-md backdrop-blur-md transition-shadow">
      <div className="mb-2 flex items-center justify-between">
        <div className="truncate font-medium" title={title}>{title}</div>
        <span className={`rounded px-2 py-0.5 text-xs ${badge}`}>
          {status === "success" ? "Success" : status === "error" ? "Error" : status === "loading" ? "Loading…" : "Idle"}
        </span>
      </div>

      <div className="relative">
        {status === "loading" && (
          <div className="animate-pulse space-y-2">
            <div className="h-4 w-3/5 rounded-md bg-white/10" />
            <div className="h-4 w-4/5 rounded-md bg-white/10" />
            <div className="h-24 w-full rounded-md bg-white/10" />
          </div>
        )}
        {status !== "loading" && (
          <div className="max-h-[400px] overflow-y-auto whitespace-pre-wrap text-sm text-neutral-200">
            {status === "error" ? error : content || ""}
          </div>
        )}
      </div>

      <div className="mt-3 flex items-center gap-2">
        <button
          onClick={() => content && navigator.clipboard.writeText(content)}
          className="rounded border border-white/10 bg-neutral-900 px-2 py-1 text-xs text-neutral-200 transition-all hover:bg-white/5"
        >
          Copy
        </button>
        <button
          onClick={() => {
            if (!content) return;
            const blob = new Blob([content], { type: "text/plain;charset=utf-8" });
            const url = URL.createObjectURL(blob);
            const a = document.createElement("a");
            a.href = url;
            a.download = `${title.replace(/[^a-z0-9-_]/gi, "_")}.txt`;
            a.click();
            URL.revokeObjectURL(url);
          }}
          className="rounded border border-white/10 bg-neutral-900 px-2 py-1 text-xs text-neutral-200 transition-all hover:bg-white/5"
        >
          Download
        </button>
      </div>
    </div>
  );
}
