"use client";
import React from "react";

type Props = {
  prompt: string;
  onChange: (v: string) => void;
  onSubmit: () => void;
  disabled?: boolean;
  onAttach?: (dataUrl: string | null, file?: File | null) => void;
  onNewChat?: () => void;
  onSaveChat?: () => void;
};

export default function PromptInputBar({ prompt, onChange, onSubmit, disabled, onAttach, onNewChat, onSaveChat }: Props) {
  return (
    <div className="composebar sticky bottom-0 z-30 border-t border-white/10 bg-black/70 backdrop-blur supports-[backdrop-filter]:bg-black/60">
      <div className="mx-auto max-w-5xl p-4">
        <div className="flex flex-col gap-3">
          <textarea
            value={prompt}
            onChange={(e) => onChange(e.target.value)}
            placeholder="Enter a prompt..."
            className="w-full resize-none min-h-[120px] rounded-lg p-4 border border-white/10 bg-neutral-900 text-neutral-100 placeholder:text-neutral-400 shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500/40"
          />

          {/* Hidden file input for image attachment */}
          <input
            id="compose-file-input"
            type="file"
            accept="image/*"
            className="hidden"
            onChange={async (e) => {
              const file = e.target.files?.[0];
              if (!file) {
                onAttach?.(null, null);
                return;
              }
              const reader = new FileReader();
              reader.onload = () => onAttach?.(String(reader.result), file);
              reader.readAsDataURL(file);
            }}
          />

          <div className="flex items-center gap-2">
            <button
              aria-label="Submit prompt"
              title="Submit prompt"
              onClick={onSubmit}
              disabled={disabled}
              className="submit-btn inline-flex h-11 w-11 items-center justify-center rounded-lg bg-gradient-to-r from-indigo-600 via-fuchsia-600 to-rose-600 text-white shadow-sm transition-all duration-200 hover:opacity-90 disabled:opacity-60"
            >
              {/* Paper plane */}
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M22 2L11 13" />
                <path d="M22 2L15 22l-4-9-9-4 20-7z" />
              </svg>
            </button>
            <button
              type="button"
              aria-label="Prompt boost"
              title="Prompt boost"
              className="icon-btn inline-flex h-11 w-11 items-center justify-center rounded-lg border border-white/10 bg-neutral-900 text-neutral-200 transition-all duration-200 hover:bg-white/5"
            >
              {/* Rocket */}
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M5 13l4 4" />
                <path d="M14 4s6 1 6 7-6 9-6 9-6-3-6-9 6-7 6-7z" />
                <circle cx="12" cy="9" r="2" />
              </svg>
            </button>
            <button
              type="button"
              aria-label="Attach file"
              title="Attach file"
              className="icon-btn inline-flex h-11 w-11 items-center justify-center rounded-lg border border-white/10 bg-neutral-900 text-neutral-200 transition-all duration-200 hover:bg-white/5"
              onClick={() => document.getElementById("compose-file-input")?.click()}
            >
              {/* Paperclip */}
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M21.44 11.05l-8.49 8.49a6 6 0 01-8.49-8.49l9.9-9.9a4 4 0 015.66 5.66l-9.9 9.9a2 2 0 01-2.83-2.83l8.49-8.49" />
              </svg>
            </button>
            <button
              type="button"
              aria-label="Start new chat"
              title="Start new chat"
              className="icon-btn inline-flex h-11 w-11 items-center justify-center rounded-lg border border-white/10 bg-neutral-900 text-neutral-200 transition-all duration-200 hover:bg-white/5"
              onClick={onNewChat}
            >
              {/* Plus icon */}
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <line x1="12" y1="5" x2="12" y2="19" />
                <line x1="5" y1="12" x2="19" y2="12" />
              </svg>
            </button>
            <button
              type="button"
              aria-label="Save chat"
              title="Save chat"
              className="icon-btn inline-flex h-11 w-11 items-center justify-center rounded-lg border border-white/10 bg-neutral-900 text-neutral-200 transition-all duration-200 hover:bg-white/5"
              onClick={onSaveChat}
            >
              {/* Floppy disk icon */}
              <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M19 21H5a2 2 0 01-2-2V5a2 2 0 012-2h11l5 5v11a2 2 0 01-2 2z"/>
                <polyline points="17 21 17 13 7 13 7 21"/>
                <polyline points="7 3 7 8 15 8"/>
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
